/* src/components/DummyScreen/DummyScreen.css */
.storeapps-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
  padding: 20px;
}

.storeapps-content {
  max-width: 600px;
}

.storeapps-content h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.storeapps-content p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

.storeapps-content img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}
